#root {
   padding:0 10px;
   margin:0 auto;
   box-sizing:border-box;
}
.uk-label {
   width:50px;
   vertical-align:1px;
   position:absolute;
   top:8px;
   left:10px;
}
.uk-container {
   font-size:1.2em;
   overflow-y:auto;
}
#media-container {
   background:radial-gradient(white, #fafafa);
   max-height:55vh;
   margin-bottom:10px;
   box-sizing:border-box;
   padding:5px;
   border:5px inset rgba(108,83,78,0.7);
   text-align:center;
}
#steps-container {
   height:55vh;
   padding-left:5px;
   padding-right:5px;
   padding-top:3px;
   overflow-y:scroll;
}
#steps-container::-webkit-scrollbar {
   width:8px;
   box-shadow:inset 0 0 6px rgba(0,0,0,0.3);
}
#steps-container::-webkit-scrollbar-thumb {
   background:linear-gradient(rgba(108,83,78,0.6), rgba(113,146,190,0.3));
   border-top-left-radius:2px;
   border-bottom-left-radius:2px;
}
.uk-card {
   min-height:12vh;
   border-top-right-radius:5px;
   border-bottom-right-radius:5px;
   cursor:pointer;
   box-shadow:0 0 3px #aaa;
   text-align:justify;
   color:black;
}
.step-text {
   margin-bottom:0px;
   margin-left:-10px;
}
.MathJax:focus, .mjx-chtml:focus, .MathJax_SVG:focus {
   outline:none;
}
.uk-button {
   border-radius:2px;
   margin-bottom:10px;
}
.arrow {
   position:absolute;
   display:flex;
   align-items:center;
   justify-content:center;
   width:3vh;
   height:3vh;
   top:5px;
   right:5px;
   box-shadow:0 0 2px #ccc;
   border-radius:2px;
   opacity:0.8;
}
.clckpnt {
   cursor:pointer;
}
